*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  background-color: azure;
  height: 100vh;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  columns: 1;
}
header{
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  box-shadow: 2px 3px 1px black;
  border-radius: 4px;
  flex-direction: column;
  font-size: 30px;
}
main input[type="text"]{
  margin: 0.5rem;
  padding: 0.5rem;
  width: 200px;
  height: 40px;
  font-size: 20px;
  border-radius: 4px;
}
main input[type="text"]:hover{
  background-color:#ccc;
}
.footer button{
  width: 200px;
  height: 40px;
  border-radius: 4px;
}
.footer button:hover{
  background-color: aquamarine;
}